export default {
  middleware: "auth",
  auth: "guest",
  layout: "none",

  data() {
    return {
      userName: "",
      email: "",
      password: "",
      emailAvail: "",
      eAvail: false,
      nError: false,
      eError: false,
      pError: false,
    };
  },

  methods: {
      validateUsername () {
          if (this.userName.length == 0) {
              this.nError = true
              return 'Username is Required'
          } else if (this.userName.length >= 12) {
              this.nError = true
              return 'Username is too long'
          } else if (this.userName.length <= 6) {
              this.nError = true
              return 'Username is too short'
          } else {
              this.nError = false
              return 'Username seems OK'
          }
      },
    async onSignUp() {
      try {
        let data = {
          userName: this.userName,
          email: this.email,
          password: this.password,
        };
        let response = await this.$axios.$post(
          `${process.env.DEV_BACKEND}/api/auth/email`,
          data
        );
        if (!response.success) {
          this.eAvail = true;
          this.emailAvail = response.message;
          return;
        } else {
          this.eAvail = false;
          this.emailAvail = "";
        }

        if (!this.nError && !this.eError && !this.pError) {
          let response = await this.$axios.$post(
            `${process.env.DEV_BACKEND}/api/auth/signup`,
            data
          );

          if (response.success) {
            this.$router.push("/verify");
          }
        }
      } catch (error) {
        this.nError = true
        this.pError = true;
        this.eError = true;
        console.log(error);
      }
    },
    checkEmail() {
        let regex = /[^@ \t\r\n]+@[^@ \t\r\n]+\.[^@ \t\r\n]+/
        if (!regex.test(this.email)) {
            this.eError = true
            return 'Enter a valid Email Address'
        } else {
            this.eError = false
            return 'Email Address is OK'
        }
    },
    checkPassword() {
      let letter = /[a-z]+/;
      let upper = /[A-Z]+/;
      let number = /[0-9]+/;
      let special = /[-+_!@#$%^&*., ?]+/;
      if (this.password.length == 0) {
          this.pError = true
          return 'Please Enter a strong Password'
      } else if (this.password.length < 6) {
          this.pError = true
          return 'Password must contain at least 6 characters'
      } else if (!letter.test(this.password)) {
          this.pError = true
          return 'Password must contain at least 1 lowercase letter'
      } else if (!upper.test(this.password)) {
          this.pError = true
          return 'Password must contain at least 1 uppercase letter'
      } else if (!number.test(this.password)) {
          this.pError = true
          return 'Password must contain at least 1 numerical digit'
      } else if (!special.test(this.password)) {
          this.pError = true
          return 'Password must contain at least 1 special character like !@#$%'
      } else {
          this.pError = false
          return 'Password is OK'
      }
    },
  },
};
